import React, { ComponentProps } from 'react';
import Helmet from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';
import { SiteMetadata } from 'models/site';
import { FluidObject } from 'gatsby-image';
import { useRecoilState } from 'recoil';
import { languageAtom } from 'atoms/language';

interface Props extends ComponentProps<typeof Helmet> {
  description?: string;
  image?: string;
}
interface QueryResult {
  site: {
    siteMetadata: SiteMetadata;
  };
  avatar: {
    childImageSharp: {
      fluid: FluidObject;
    };
  };
}
const SEO = ({ description = '', meta = [], title, image }: Props) => {
  const {
    site: { siteMetadata },
    avatar,
  }: QueryResult = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            siteUrl
            author {
              name
            }
          }
        }
        avatar: file(absolutePath: { regex: "/me.jpg/" }) {
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  );
  const [lang] = useRecoilState(languageAtom);

  const metaTitle = title || siteMetadata.title;
  const metaDescription = description || siteMetadata.description;
  const metaImage = image || avatar.childImageSharp.fluid.src;

  const defaultMeta = [
    {
      name: `description`,
      content: metaDescription,
    },
    {
      property: `og:title`,
      content: metaTitle,
    },
    {
      property: `og:description`,
      content: metaDescription,
    },
    {
      property: `og:image`,
      content: metaImage,
    },
    {
      property: `og:type`,
      content: `website`,
    },
    {
      property: `twitter:title`,
      content: metaTitle,
    },
    {
      property: `twitter:description`,
      content: metaDescription,
    },
    {
      proprety: `twitter:image`,
      content: metaImage,
    },
    {
      name: 'naver-site-verification',
      content: '48beb5f578053c0c5f127b4198a57270bad360ca',
    },
    {
      name: 'google-site-verification',
      content: 'oDn8TK5EJ-ZVoh3XGbemEEvoGi9dCYSffzj7VUfrG3Q',
    },
  ];

  const siteData = {
    '@context': 'https://schema.org',
    '@type': 'WebPage',
    name: siteMetadata.title,
    url: siteMetadata.siteUrl,
    mainEntityOfPage: siteMetadata.siteUrl,
    description: siteMetadata.description,
    author: {
      '@type': 'Person',
      name: siteMetadata.author.name,
    },
    creator: {
      '@type': 'Person',
      name: siteMetadata.author.name,
    },
    image: {
      '@type': 'ImageObject',
      url: `${siteMetadata.siteUrl}${avatar.childImageSharp.fluid.src}`,
    },
  };

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      defaultTitle={siteMetadata.title}
      title={title}
      titleTemplate={`%s | ${siteMetadata.title}`}
      meta={[...defaultMeta, ...meta]}
    >
      <script type="application/ld+json">{JSON.stringify(siteData)}</script>
    </Helmet>
  );
};

export default SEO;
