import { useStaticQuery, graphql } from 'gatsby';
import { SiteMetadata } from 'models/site';

export const useSiteMetadata = () => {
  const { site }: { site: { siteMetadata: SiteMetadata } } = useStaticQuery(graphql`
    query SiteMetadata {
      site {
        siteMetadata {
          title
          author {
            name
            summary
            email
          }
          description
          siteUrl
          social {
            github
            linkedin
            rss
          }
          languages
          defaultLanguage
        }
      }
    }
  `);

  return site.siteMetadata;
};
